.cart-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: auto;
    margin: 0 0 5px 0;
    padding: 10px;
    border-radius: 5px;
    background-color: #e3ebe7;
}

.cart-item p {
    font-size: 1.5rem;
    font-weight: bold;
}

.amount {
    display: flex;
    justify-content: center;
}

.amount input {
    height: 40px;
    width: 40px;
    border:3px solid rgba(0, 128, 128, 1);
    border-left: unset;
    border-right: unset;
    border-radius: unset;
    outline: none;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
}

.button-right {
    width: 32px;
    border-radius: 10px 0 0 10px;
    border:3px solid rgba(0, 128, 128, 1);
    border-right: unset;
    background-color: white;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 2rem;
    font-weight: bold;
}

.button-left {
    width: 32px;
    border-radius: 0 10px 10px 0;
    border:3px solid rgba(0, 128, 128, 1);
    border-left: unset;
    background-color: white;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 2rem;
    font-weight: bold;
}

.title h3, 
.title p {
    margin: 0;
}

@media (max-width: 992px) {
    .title h3,
    .title p {
      font-size: 1.5rem;
    }
  }