body {
  font-family: "Alegreya Sans", sans-serif;
  /* font-family: 'Cedarville Cursive', cursive;
  font-family: 'Josefin Sans', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

input,
textarea {
  border-radius: 5px;
  border: 2px solid rgba(0, 128, 128, 0.15);
}

input:focus,
input:hover,
textarea:focus,
textarea:hover {
  outline: none;
  border: 2px solid rgba(0, 128, 128, 1);
}

label {
  font-family: "Alegreya Sans", sans-serif;
  font-size: calc(1rem + 0.22vw);
  line-height: 1.5;
  color: #444a4a;
}

h1 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 63px;
}
@media (max-width: 576px) {
  h1 {
    font-size: 54px;
  }
}

h2 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 38px;
  font-weight: lighter;
  margin-top: 0;
}
@media (max-width: 992px) {
  h2 {
    margin-top: 35px;
    font-size: 38px
  }
}
@media (max-width: 375px) {
  h2 {
    font-size: calc(2.5rem - 1.7vw);
  }
}

h3 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 28px;
}

p {
  font-family: "Alegreya Sans", sans-serif;
  font-size: calc(19px + 0.22vw);
  line-height: 1.5;
  color: #444a4a;
}
@media (max-width: 992px) {
  p {
    font-size: 1.25rem;
  }
}

span {
  font-size: 1.5rem;
  font-weight: bold;
}

button,
a {
  cursor: pointer;
}