.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 25px 0;
  font-weight: bold;
}
@media (max-width: 992px) {
  .header {
    padding: unset;
  }
}

.image {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  height: 100px;
}

.image .logo {
  position: relative;
  bottom: 5px;
  width: 70px;
}
@media (max-width: 576px) {
  .image .logo {
    width: 60px;
  }
}

.image .text {
  width: 200px;
}
@media (max-width: 576px) {
  .image .text {
    width: 150px;
  }
}

@media (max-width: 992px) {
  .header > button {
    border-color: transparent;
    border-radius: 5px;
    background-color: white;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60%;
}
@media (max-width: 992px) {
  .nav {
    display: none;
  }
}

.nav p:hover {
  color: seagreen;
  cursor: pointer;
}

.button {
  width: 142px;
  height: 40px;
  font-size: 1rem;
}

.burger-menu {
  position: fixed;
  top: 25px;
  right: 10px;
  width: 50px;
  height: 50px;
  z-index: 210;
}

.cart-menu {
  position: fixed;
  top: 80px;
  right: 10px;
  width: 50px;
  height: 50px;
  z-index: 210;
}

.cart-menu-icon {
  width: 30px;
  height: 30px;
}

.cart-button {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1rem;
  width: 140px;
  height: 40px;
  padding-right: 10px;
  padding-left: 20px;
}

.cart-title {
  position: relative;
  display: flex;
  align-items: center;
}

.cart-icon {
  position: relative;
  bottom: 2px;
  width: 20px;
  height: 20px;
  margin: 0 5px;
}

.amount-status {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  border: 2px solid white;
  vertical-align: middle;
  text-align: center;
  line-height: 22px;
  font-size: 1rem;
}

.mobile-amount-status {
  position: fixed;
  top: 105px;
  right: 15px;
  width: 12px;
  height: 12px;
  border-radius: 6px;
  border: 2px solid black;
  vertical-align: middle;
  text-align: center;
  line-height: 10px;
  font-size: 15px;
  background-color: white;
  z-index: 211;
}
