.modal {
  top: 5vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 310;
  padding: 10px 0;
}

@media (max-width: 992px) {
  .modal {
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    border-radius: unset;
    overflow: auto;
  }
}

.cart {
  display: flex;
  flex-direction: column;
  max-height: 80vh;
  margin: 25px 50px 25px 25px;
  overflow: auto;
}
@media (max-width: 992px) {
  .cart {
    flex-direction: column;
    max-height: unset;
    overflow: unset;
  }
}

.cart-empty {
  text-align: center;
  vertical-align: baseline;
}

.cart-items {
  display: flex;
  flex-direction: column;
  overflow: auto;
}
@media (max-width: 992px) {
  .cart-items {
    margin: 0 40px 0 0;
  }
}
@media (max-width: 425px) {
  .cart-items {
    margin: unset;
  }
}


.order {
  height: 100%;
}
@media (max-width: 992px) {
  .order {
    height: unset;
  }
}
