.order-form-container {
  display: flex;
  align-items: stretch;
  margin: 0 0 5px 0;
}
@media (max-width: 768px) {
  .order-form-container {
    flex-direction: column;
  }
}

.order-info {
  display: flex;
  flex-direction: column;
}

.order-recieving {
  display: unset;
  margin: 0 0 0 25px;
}
@media (max-width: 768px) {
  .order-recieving {
    margin: unset;
  }
}

.order-button {
  margin: 5px 0 5px 0;
}

.address {
  display: flex;
  flex-direction: column;
}
