.item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 430px;
  max-width: 320px;
  margin: 45px 15px;
  padding: 10px 45px 20px 45px;
  border-radius: 25px;
  box-shadow: 0 5px 10px #d3dad5;
  background-color: #ffffff;
  text-align: center;
}
@media (max-width: 767px) {
  .item {
    margin: 45px auto;
  }
}

.item h3 {
  flex: flex-start;
}

@media (max-width: 403px) {
  .item h3 {
    margin-bottom: 0px;
    font-size: 8vw;
  }
  .item p {
    margin: 0px 0px;
    font-size: calc(1rem + 0.5vw);
  }
}

.actions {
  flex: flex-end;
  display: flex;
  align-items: center;
}

.order-button {
  width: 180px;
  border-radius: 7px 0 0 7px;
}

.cart-button {
  position: relative;
  width: 55px;
  border-radius: 0 7px 7px 0;
  background-color: #37756f;
  padding: 0;
}

.cart-icon {
  width: 20px;
  height: 20px;
  margin: auto;
}

.amount-status {
  position: absolute;
  left: 27px;
  top: 27px;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background-color: #2d635e;
  vertical-align: middle;
  text-align: center;
  line-height: 20px;
  font-size: 1rem;
}